<template>
<div>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-menu"></i></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-container>
      <el-main style="padding-left: 0px;padding-top: 0px">
        <!--  -->
        <div>
          <el-card style="margin-top:30px;">
            <el-form ref="busfrom" :model="busfrom" :rules="rules" label-width="140px">
                <el-form-item label="">
                    <el-select v-model="busfrom.projectId" placeholder="">
                        <el-option v-for="item in restaurants" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <!-- <el-form-item label="">
                    <el-select v-model="busfrom.yuBaoJiaId" placeholder="">
                        <el-option v-for="item in yubaolist" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-form-item> -->
                <el-form-item label="" prop="productName">
                    <el-input v-model="busfrom.productName" placeholder="" style="width:20%"></el-input>
                </el-form-item>
                <el-form-item label="" prop="carType">
                    <el-input v-model="busfrom.carType" placeholder="" style="width:20%" ></el-input>
                </el-form-item>
                <el-form-item label="" prop="clientCompanyName">
                    <el-input v-model="busfrom.clientCompanyName" placeholder=""  style="width:20%"></el-input>
                </el-form-item>
                <el-form-item label="" prop="contactPerson">
                    <el-input v-model="busfrom.contactPerson" placeholder="" style="width:20%" ></el-input>
                </el-form-item>
                <el-form-item label="" prop="contactPhone">
                    <el-input v-model="busfrom.contactPhone" placeholder="" style="width:20%" ></el-input>
                </el-form-item>
                <el-form-item label="">
                    <!-- <el-upload class="upload-demo"> -->
                    <div style="border: 1px dashed #d9d9d9;width:60%;min-height:128px;">
                        <el-form-item label="" style="margin-top:15px;" prop="wenLiPrice">
                            <el-input-number  v-model="busfrom.wenLiPrice" controls-position="right" :min="0"></el-input-number>
                        </el-form-item>
                        <el-form-item label="" style="margin-top:15px;" prop="hanJiePrice">
                            <el-input-number  v-model="busfrom.hanJiePrice" controls-position="right" :min="0"></el-input-number>
                        </el-form-item>
                        <el-form-item label="" style="margin-top:15px;" prop="additionPrice">
                            <el-input-number  v-model="busfrom.additionPrice" controls-position="right" :min="0"></el-input-number>
                        </el-form-item>
                        <el-form-item label="" style="margin-top:15px;" prop="total">
                            <span><el-tag type="warning">  </el-tag></span>
                        </el-form-item>
                    </div>
                </el-form-item>
                <el-form-item label=""  style="width:30%"prop="payType">
                    <el-input v-model="busfrom.payType"></el-input>
                </el-form-item>
                <el-form-item label=""style="width:30%" prop="expectTime">
                    <el-input v-model="busfrom.expectTime"></el-input>
                </el-form-item>
                <el-form-item label="" prop="remark">
                    <el-input type="textarea" style="width:30%"v-model="busfrom.remark"></el-input>
                </el-form-item>
            </el-form>
            <div class="card-bottom" style="float:right;">
              <el-button type="primary" @click="submitForm('busfrom')"> </el-button>
              <el-button  @click="resetForm('busfrom')"> </el-button>
            </div>
          </el-card>
        </div>
      </el-main>
    </el-container>
  </div>
</div>
</template>
<script>
export default {
  props: {
    title: String
  },
  data() {
    return {
      // upload:'http://39.107.78.95:8080/jlmanager/comm/upload/image',
      // upload: 'http://localhost:8885/config/upload/image',
      table_loading: false,
      restaurants: [],
      yubaolist: [],
      busfrom: {
        tax: '17',
        addUserId: '',
        addUserName: '',
        wenLiPrice: "0",
        hanJiePrice: '0',
        additionPrice: '0',
        expectTime: "12",
        payType: ""
      },
      rules: {
        productName: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 0,
            max: 255,
            message: ' 0  255 ',
            trigger: 'blur'
          }
        ],
        wenlidaima: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 0,
            max: 255,
            message: ' 0  255 ',
            trigger: 'blur'
          }
        ],
        carType: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        clientCompanyName: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        contactPerson: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        contactPhone: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        payType: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        expectTime: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        remark: [{
          min: 0,
          max: 2048,
          message: ' 0  2048 ',
          trigger: 'blur'
        }]
      },
    }
  },
  methods: {
    submitForm(formName){
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let self = this;
          let parm = self.busfrom;
          parm.addUserId = this.uid;
          parm.addUserName = this.name;
          this.postRequest("/offer/bus/add",parm).then(resp => {
            if (resp && resp.status == 200 && resp.data.code == 0) {
              let id = resp.data.data.id;
              if(id > 0) {
                self.$message.success("");
                this.$router.push('/offer/bus/list')
              }else {
                self.$message.error("");
              }
            } else {
              self.$message.error("");
            }
          })
        } else {
          self.$message.error('');
          return false;
        }
      });
    },
    loadAllProject() {
        let self = this
        this.getRequest("/project/list/added?userId=" + this.uid).then(resp => {
          if (resp && resp.status == 200 && resp.data.code == 0) {
                let item = []
                let re = resp.data.data.addedProject
                console.log(re);
                for (var i = 0; i < re.length; i++) {
                    let elem = {
                        label: '',
                        value: ''
                    }
                    elem.label = re[i].projectName
                    elem.value = re[i].id
                    item.push(elem);
                }
                self.restaurants = item;
            } else {
                console.log("");
            }
        });
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  mounted: function() {
    this.username = localStorage.getItem('cp_username');
    this.uid = localStorage.getItem('cp_uid');
    this.name = localStorage.getItem('cp_name');
    this.loadAllProject();
  },
  computed: {
      // totalPrice: function() {
      //     var total = 0;
      //     total = this.busfrom.texturingprice + this.busfrom.polishingprice + this.busfrom.extraprice
      //     this.busfrom.totleprice = total
      //     return total;
      // }
  }
}
</script>



